input {
    height: 32px;
    background-color: transparent;
    border: none;
    color: white;
    padding-left: 8px;
    padding-right: 8px;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
}
input::-webkit-input-placeholder {
    font-family: 'Exo 2', sans-serif;
    font-size: 0.8rem;
    color: grey;
}
input:-ms-input-placeholder {
    font-family: 'Exo 2', sans-serif;
    font-size: 0.8rem;
    color: grey;
}
input::-ms-input-placeholder {
    font-family: 'Exo 2', sans-serif;
    font-size: 0.8rem;
    color: grey;
}
input::placeholder {
    font-family: 'Exo 2', sans-serif;
    font-size: 0.8rem;
    color: grey;
}
input:focus {
    outline: none;
}
input:focus::-webkit-input-placeholder {
    color: grey;
}
input:focus:-ms-input-placeholder {
    color: grey;
}
input:focus::-ms-input-placeholder {
    color: grey;
}
input:focus::placeholder {
    color: grey;
}



